.container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 80%;
  margin: 0 auto;
  padding-top: 20px;
}

.left, .right {
  width: 45%;
  padding: 20px;
  border: 2px solid #ccc; /* Adds a box around each section */
  border-radius: 10px; /* Rounded corners for the box */
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1); /* Light shadow for depth */
}

.divider {
  width: 5%;
  border-left: 2px solid #333; /* Darker divider between sections */
  height: auto; /* Divider height will adjust with content */
  margin: 0 10px;
}

.left form {
  display: flex;
  flex-direction: column;
}

.left input {
  margin-bottom: 10px;
  padding: 8px;
  font-size: 16px;
}

.left button {
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
}

.left button:hover {
  background-color: #45a049;
}

.right ul {
  list-style-type: none;
  padding: 0;
}

.right li {
  margin-bottom: 20px; /* Adds space between each person in the list */
}

h3 {
  text-align: center;
}
